body {
  overflow: hidden;
}

body {
  background: #141318;
}

.App img {
  position: absolute;
  width: 100%;
  height: auto;
  min-width: 1024px;
  min-height: 100%;
  
}

@media screen and (max-width: 1024px) {
  .App img {
    left: 50%;
    margin-left: -512px; 
  }
}

.visible {
  visibility: visible;
}

.invisible {
  visibility: hidden;
}

.toggle {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  z-index: 1000;
  display: flex;
  border-radius: 50px;
  box-shadow: 0 0 0 2px #fff;
  box-sizing: border-box;
}

.credit {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  z-index: 1000;
  font-size: 10px;
}

a, a:hover, a:visited {
  color: #fff;
}

input {
	position: absolute !important;
	height: 1px;
	width: 1px;
	border: 0;
}

.active {
	background-color: rgba(0, 132, 255, 0.6);
  box-shadow: 0 0 8px 4px rgba(255, 255, 255, .5), 0 0 0 2px #fff;
  border-radius: 50px;
  /* border: 2px solid #fff; */
}

label {
	background-color: transparent;
	color: #fff;
	font-size: 14px;
  font-weight: bold;
	text-align: center;
	padding: 8px 16px;
  min-width: 60px;
	margin-right: -1px;
  cursor: pointer;
}